.filter, .sort{
    display: flex;
    align-items: center;
    justify-content: left;
    label{
        margin-right: 15px;
    }

}
h3{
    width: 20%;
    text-align: left;
}
.item-enter {
    opacity: 0;
    transform: translateY(-10px);
  }
  
  .item-enter-active {
    opacity: 1;
    transform: translateY(0);
    transition: opacity 500ms, transform 500ms;
  }
  
  .item-exit {
    opacity: 1;
    transform: translateY(0);
  }
  
  .item-exit-active {
    opacity: 0;
    transform: translateY(-10px);
    transition: opacity 500ms, transform 500ms;
  }
    /* Для экранов меньше 600px */
    @media (max-width: 599px) {
      .filter, .sort{
        display: flex;
        flex-direction: column;
       align-items: start;
       label{
       margin-bottom: 5px;
    }
    
    }
  
    }
    